import React from "react";

interface ButtonProps {
  text: string;
  onClick?: () => void;
  type?: "button" | "submit" | "reset";
}

const Button: React.FC<ButtonProps> = ({ text, onClick, type = "button" }) => {
  return (
    <button
      type={type}
      onClick={onClick}
      className="w-full py-3 px-4 border rounded-lg font-bold text-primary-900 border-primary hover:bg-primary hover:text-white transition"
    >
      {text}
    </button>
  );
};

export default Button;
