import React from "react";

interface InputProps {
  label: string;
  placeholder: string;
  icon: string;
  type?: "text" | "email" | "password";
  value: string;
  onChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
}

const Input: React.FC<InputProps> = ({ label, placeholder, icon, type = "text", value, onChange }) => {
  return (
    <div className="relative">
      <label className="block text-sm font-semibold text-primary-700 mb-1">
        {label}
      </label>
      <div className="flex items-center border-b border-neutral-600">
        <img src={icon} alt="icon" className="w-5 h-5 mr-2" />
        <input
          type={type}
          value={value}
          onChange={onChange}
          placeholder={placeholder}
          className="w-full py-2 text-sm placeholder:text-neutral-600 outline-none"
        />
      </div>
    </div>
  );
};

export default Input;
